.chart-circle {
  position: relative;
  text-align: center;
  .chart {
    position: relative;
    background-color: #a1a1a1;
    border-radius: 50%;
    height: 120px;
    margin: 0 auto;
    padding: 6px;
    -ms-transform: rotate(180deg);
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg);
    width: 120px;
    > .item-progress {
      -webkit-transition: all 1.3s ease;
      -moz-transition: all 1.3s ease;
      -o-transition: all 1.3s ease;
      transition: all 1.3s ease;
      display: inline-block;
      height: 20px;
      overflow: hidden;
      position: relative;
      width: 108px;
      &:before {
        border-radius: 106px;
        background-color: #363636;
        content: '';
        display: block;
        height: 108px;
        width: 108px;
      }
    }
  }
  > .percent {
    display: block;
    height: 40px;
    left: 50%;
    margin-left: -20px;
    margin-top: -20px;
    position: absolute;
    width: 40px;
    font-family: 'Montserrat-SemiBold';
  }
}

.chart-bar>.percent, .chart-circle>.percent {
  line-height: 40px!important;
  top: 50%;
  text-align: center;
}