#home {
  position: relative;
  height: calc(100% - 60px);
  background-image: url('../images/bg-home.jpg');
  padding: 0;
  @include tablet() {
    height: 100%;
  }
  .container {
    position: relative;
  }
  .home-content {
    text-align: center;
    // align-items: center;
    // justify-content: center;
    color: #fff;
    h1 {
      color: #fff;
      font-size: 4.375rem;
      margin: 10px 0;
      letter-spacing: $ls-normal;
      font-family: 'Montserrat-SemiBold';
      @include mobile() {
        font-size: 3rem;
      }
    }
    h2 {
      margin: 0;
      font-size: 1.75rem;
      @include mobile() {
        font-size: 1.5rem;
      }
    }
  }
  .social-media {
    position: relative;
    margin: 1rem auto;
    .social-icon {
      position: relative;
      display: inline-block;
      height: 35px;
      width: 35px;
      text-align: center;
      font-size: 18px;
      color: #333;
      background: #fff;
      border-radius: 50%;
      margin-right: 5px;
      box-shadow: 0px 1px 4.7px 0.3px rgba(0, 0, 0, 0.24);
      transition: all ease .2s;
      &:hover {
        background-color: #333;
        color: #fff;
        transition: all ease .2s;
      }
      .fa {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }
}