.navbar {
  margin: 0;
  border-radius: 0;
  background-color: #171717;
  border-bottom: 1px solid #242424;
  z-index: 1000 !important;
  @include tablet() {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
  }
}
.navbar-header {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  height: 60px;
  @include tablet() {
    width: 100%;
  }
  .navbar-brand {
    color: #fff;
    font-family: 'Montserrat-SemiBold';
  }
  .navbar-toggler {
    outline: none;
    border-color: rgba(255,255,255,.3);
  }
}
.navbar-collapse {
  @include tablet() {
    border-top: 1px solid rgba(255,255,255,.15);
    padding-bottom: 12px;
    padding-top: 8px;
  }
  .navbar-nav {
    text-align: center;
    @include tablet() {
      text-align: left;
    }
    .nav-item {
      padding: 10px 20px;
      @include tablet() {
        padding: 10px 0;
      }    
      .nav-link {
        position: relative;
        display: inline-block;
        padding: 2px 0;
        font-size: 12px;
        font-family: 'Montserrat-Regular';
        color: #fff;
        opacity: .90;
        letter-spacing: .125rem;
        text-transform: uppercase;
        &.active, &:hover {
          border-bottom: 1px solid #fff;
          background-color: transparent;
        }
      }
    }
  }
}