#portfolio {
  background-color: $dark-grey;
}

.gallery-filters {
  list-style: none;
  padding-left: 0;
  text-align: center;
  margin: 2rem auto;
  .filter-item {
    cursor: pointer;
    color: #fff;
    display: inline-block;
    padding: 8px 12px;
    margin: 0 8px;
    border-radius: 4px;
    text-transform: uppercase;
    font-size: 12px;
    font-family: 'Montserrat-SemiBold';
    transition: all ease .5s;
    &.active, &:hover {
      background-color: #fff;
      color: #000;
      transition: all ease .5s;
    }
  }
}

.gallery-grid figure .gallery-img img,
.gallery-grid figure .gallery-link a,
.gallery-grid figure .gallery-title span,
.gallery-grid figure .gallery-title:before {
  -webkit-transition: all .3s ease;
  -moz-transition: all .3s ease;
  -o-transition: all .3s ease;
  transition: all .3s ease
}

.gallery-grid {
  figure {
    position: relative;
    width: 100%;
    height: 18rem;
    margin: 0;
    margin-bottom: 1.5rem;
    overflow: hidden;
    &:hover {
      .gallery-link a {
        left: 40px;
        opacity: 1;
        &.zoom {
          left: 0
        }
      }
      .gallery-title:before {
        background: rgba(0, 0, 0, .6)
      }
      .gallery-title span {
        margin-top: 0;
        opacity: 1
      }
    }
    .gallery-link {
      height: 48px;
      left: 50%;
      margin-left: -44px;
      margin-top: -48px;
      position: absolute;
      top: 50%;
      width: 88px;
      z-index: 15;
      a {
        left: 0;
        opacity: 0;
        position: absolute;
        top: 0;
        &.zoom {
          left: 40px
        }
      }
    }
    .gallery-title {
      &:before {
        content: '';
        background: 0 0;
        display: block;
        height: 100%;
        left: 0;
        position: absolute;
        top: 0;
        width: 100%;
        z-index: 5
      }
      span {
        display: block;
        height: 48px;
        line-height: 48px!important;
        margin-top: -48px;
        opacity: 0;
        position: absolute;
        text-align: center;
        font-family: 'Montserrat-Medium';
        top: 50%;
        width: 100%;
        z-index: 10
      }
    }
    .gallery-grid {
      img {
        display: block;
        position: relative;
        top: 0;
        width: 100%
      }
    }
  }
}
